/*ПЕРЕМЕННЫЕ*/

 :root{
  --primary-color:#F4F3EE ;
  --text-primary-color:#d1d0cd ;
  --akcent-color:#D4854D;
  --akcent-color-2:#A44A3F;
  --color-error: #E74C3C;
  --color-succes:#24ad5d;
  --secondary-color:  #D8E4E4;
  --text-color: #444444;
  --background-color: #FFFFFF;
  --br:6px
}

/*ОСНОВНЫЕ БЛОКИ*/
*{
  box-sizing: border-box;
}
body{
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
  overflow: hidden;
}
#root{
  display: flex;
  font-family: 'Poppins', sans-serif;
  font-size: 14px;
  background-color: var(--primary-color);
  height: 100vh;
}
.content-container{
  height: 100vh;
  flex-basis: 100%;
  display: flex;
  gap: 10px;
  padding: 5px;
  overflow: hidden;
}

.wrapper-content{
  width: 75%;
  background-color: var(--background-color);
  border-radius: var(--br);
  padding: 5px;
  overflow-y: auto;
}

.wrapper-content::-webkit-scrollbar {
  width: 8px;
  border: 1px solid var(--secondary-color);
  border-radius: var(--br);
}

.wrapper-content::-webkit-scrollbar-thumb {
  background:var(--akcent-color);
  border-radius: var(--br);
}

.wrapper-content::-webkit-scrollbar-thumb:hover {
  background:var(--akcent-color);
  border-radius: var(--br);
}

/*КОНТЕЙНЕР ФИЛЬТРАЦИИ*/
.filter-container{
  width: 25%;
  padding: 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 3px;
  background-color: var(--background-color);
  border-radius: var(--br);
}
 .filter-column {
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

 .filter-header{
  display: flex;
  justify-content: space-between;
  padding: 5px;
  background-color: var(--secondary-color);
  border-radius: var(--br);
 }

 .filter-list {
  list-style: none;
  padding: 0;
  margin: 5px 0px 0px 0px;
  max-height: calc(23vh - 5px);
  overflow-y: auto;
  overflow-x: hidden;
}

.filter-list:last-child{
  margin-bottom: 5px;
}

 .filter-list::-webkit-scrollbar {
  margin-left: 5px;
  width: 8px;
}

.filter-list::-webkit-scrollbar-thumb {
  background:var(--akcent-color);
  border-radius: var(--br);
}

.filter-list::-webkit-scrollbar-thumb:hover {
  background:var(--akcent-color);
}

.filter-option {
  overflow: hidden;
  cursor: pointer;
  padding: 5px;
  margin-bottom: 3px;
  background-color: var(--primary-color);
  border: none;
  border-radius: var(--br);
  white-space: nowrap; 
}

.filter-option:hover {
  background-color: var(--text-primary-color);
  transition: all 0.3s ease;
}

.filter-option.selected {
  background-color: var(--akcent-color);
  color: var(--background-color);
  border-color: transparent;
}

.seria-column,
.comp-column {
  flex: 1 1 auto;
  width: 100%;
}

.color-column,
.profileColor-column {
  flex: 1 1 auto;
  width: calc(50% - 5px);
}

.height-column,
.width-column,
.depth-column{
  flex: 1 1 auto;
  width: calc(30% - 5px);
}
 .seria-column .filter-list {
  display: flex;
  flex-wrap: wrap;
  gap: 5px;
 }

 .clear-button{
  background-color: var(--background-color);
  border: none;
  border-radius: var(--br);
  font-weight: 600;
  color: var(--secondary-color);
  transition: all 0.3s ease;
  cursor: pointer;
}

.clear-button:hover{
  font-weight: 600;
  color: var(--akcent-color);
}

.clear-button.active {
  background-color: var(--background-color);
  color: var(--color-error);
  font-weight: bolder;
}

/*КОНТЕЙНЕР ЗАГАЛОВКА*/
.header {
  position: sticky;
  top: 0;
  display: flex;
  align-items: flex-start;
  gap: 10px;
  margin-bottom: 5px;
  border-radius: var(--br);
  padding-bottom: 5px;
  background-color: var(--background-color);
  box-shadow: 0px 0px 5px 8px var(--background-color);
  z-index: 100;
}

.header-buttons{
  display: flex;
  align-items: center;
  gap:5px;
  background-color: var(--secondary-color);
  color: var(--text-color);
  padding: 5px;
  border: none;
  border-radius: var(--br);
  cursor: pointer;
  transition: all 0.3s ease;
}

 .header-buttons:hover{
  background-color: var(--akcent-color);
  color: var(--background-color);
  transition: all 0.3s ease;
 } 

 .header-buttons:hover .icons{
  background-color: var(--background-color);
  color: var(--akcent-color);
  transition: all 0.3s ease;
 }

 .icons{
  background-color: var(--background-color);
  color: var(--akcent-color);
  padding: 5px;
  border-radius: var(--br);
  transition: all 0.3s ease;
 }

 .reset{
  margin-right: auto;
 }

/*КОНТЕЙНЕР КАРТОЧЕК*/
.image-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-self: flex-start;
  align-items: flex-start;
  gap: 10px;
  background-color: var(--background-color);
  border-radius: var(--br);
}

/*КАРТОЧКА*/
.image-card {
  position: relative;
  align-self: flex-start;
  align-items: flex-start;
  height: 220px;
  min-width: 250px;
  max-width: 300px;
  background-color: var(--background-color);
  padding: 10px;
  border-radius: var(--br);
  border: 1px solid var(--secondary-color);
  box-shadow: 0px 3px 5px hsl(180, 18%, 87%, 0.6);
  cursor: pointer;
  transition: transform 0.3s ease;
  overflow: hidden;
}
 
 .image-card:hover .card-title p{
  color: var(--text-color);
  background-color: var(--secondary-color);
  padding: 5px 7px;
  border-radius: var(--br);
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
}
.image-card:hover .icons-download{
  color: var(--background-color);
  background-color: var(--akcent-color);
  padding: 5px 7px;
  border-radius: var(--br);
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
}
.image-card:hover .card-size{
  color: var(--primary-color);
}
.image-card:hover {
  transform: translateY(0);
}
.card-content{
  display: flex;
  justify-content: space-between;
  opacity: 0;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.card-content.in-view {
  opacity: 1;
}

.image-card img {
  display: block;
  object-fit: cover;
  max-width: 100%;
  height: auto;
}
.placeholder{
  width: 100%;
  font-size: 40px;
  text-align: center;
  color: var(--secondary-color);
}

.card-title{
  display: flex;
  align-items: flex-start ;
  justify-content: space-between;
  margin-top: 0;
}

.card-title p {
  background-color: var(--primary-color);
  color: var(--akcent-color);
  padding: 5px 7px;
  margin-top: 0;
  border-radius: var(--br);
}

.icons-download{
  display: block;
  color: var(--akcent-color);
  background-color: var(--primary-color);
  padding: 5px 7px;
  border-radius: var(--br);
  font-size: small;
  font-weight: 600;
  transition: all 0.3s ease;
  cursor: pointer;
  border: none;
}

.card-size{
  align-self: center;
  text-align: start;
  padding: 5px;
  border-radius: var(--br);
  color: var(--text-color);
}
.image-details {
  display: flex;
  flex-direction: column;
  align-self: flex-end;
  word-wrap: break-word;
  gap: 5px;
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: #d8e4e4cb;
  color: var(--text-color);
  padding: 10px;
  box-sizing: border-box;
  transform: translateY(100%);
  transition: transform 0.3s ease;
  border-radius: var(--br);
}
.image-details p{
  margin: 0;
}

.image-card:hover .image-details {
  transform: translateY(0);
}
/*Модальные окна*/

.modal {
  min-width:70%;
  max-width: 90%;
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 50%;
  left: 50%;
  background-color: var(--background-color);
  padding: 20px;
  border-radius: var(--br);
  box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.3);
  z-index: 9999;
  transform: translate(-50%, -50%);
}

.modal-header{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.modal-footer{
  display: flex ;
  align-items: center;
  gap: 10px;
}

.list-files {
  list-style: none;
  padding: 5px;
  max-height: 75vh;
  overflow-y: auto;
  overflow-x: hidden;
}

.list-files-upload{
  list-style: none;
  padding: 5px;
  max-height: 75vh;
  overflow-y: auto;
  overflow-x: hidden;
}
.list-files li {
  margin-bottom: 10px;
  padding: 5px;
  display: flex;
  flex-wrap: nowrap;
  word-wrap: break-word;
  align-items: center;
  background-color: var(--primary-color);
  border: 1px solid transparent;
  border-radius: var(--br);
  justify-content: space-between;
  transition: all 0.3s, color 0.3s;
}
.list-files-upload li{
  margin-bottom: 10px;
  padding: 5px;
  display: flex;
  flex-wrap: nowrap;
  word-wrap: break-word;
  align-items: center;
  background-color: var(--primary-color);
  border: 1px solid transparent;
  border-radius: var(--br);
  justify-content: space-between;
  transition: all 0.3s, color 0.3s;
}

.custom-file-upload:first-child {
  color: var(--background-color);
  background-color: var(--akcent-color);
  border: none;
  border-radius: var(--br);
  display: inline-block;
  padding: 6px;
  cursor: pointer;
  transition: all 0.3s, color 0.3s;
}

.custom-file-upload:not(:first-child) {
  color: var(--text-primary-color);
  background-color: var(--background-color);
  border: none;
  border-radius: var(--br);
  display: inline-block;
  padding: 6px 12px;
  cursor: pointer;
  transition: all 0.3s, color 0.3s;
}

.input-add-files {
  display: none;
}

.file-preview {
max-width: 100px;
height: auto;
border-radius: var(--br);
}

.list-files li:hover {
  border: 1px solid var(--akcent-color);
  border-radius: var(--br);
  transition: all 0.3s, color 0.3s;
  cursor:pointer;
}

.list-image {
  width: 70px;
  align-self: flex-end;
}

.success {
  display: block;
  border:1px solid var(--color-succes) !important;
  color: var(--color-succes);
}

.error{
  border: 1px solid var(--color-error) !important;
  color: var(--color-error);
}

.success-message{
  display: block;
  color: var(--color-succes);
  margin-left: 10px;
}
.error-message {
  display: block;
  color: var(--color-error);
  margin-left: 10px;
}

.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 9998;
}

.button-public, .modal .close{
  background-color: var(--akcent-color);
  color: var(--background-color);
  border: none;
  border-radius: var(--br);
  padding: 10px;
  cursor: pointer;
  font-size: 14px;
  transition: background-color 0.3s, color 0.3s;
}

.button-public{
  margin-right: auto;
}

.modal .close:hover, .modal .button-public:hover{
  background-color: var(--akcent-color-2);
  color: var(--background-color);
}

.button-public:disabled{
  background-color: var(--secondary-color);
  transition: background-color 0.3s, color 0.3s;
}

.styled-label{
  display: flex;
  align-items: center;
  cursor: pointer;
}

.styled-checkbox {
  appearance: none;
  width: 1.2rem;
  height: 1.2rem;
  background-color: var(--background-color);
  border: 1px solid var(--secondary-color);
  border-radius: var(--br);
  cursor: pointer;
  outline: none;

}
.styled-checkbox:hover {

  border: 1px solid var(--text-primary-color);
}

.styled-checkbox:checked {
  background-color: var(--akcent-color);
  border-color: var(--akcent-color);
}

.modal h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: var(--akcent-color);
}
.list-files::-webkit-scrollbar,
.list-files-upload::-webkit-scrollbar {
  margin-left: 5px;
  width: 8px;
  border: 1px solid var(--secondary-color);
  border-radius: var(--br);
}

.list-files::-webkit-scrollbar-thumb,
.list-files-upload::-webkit-scrollbar-thumb {
  background:var(--akcent-color);
  border-radius: var(--br);
}

.list-files::-webkit-scrollbar-thumb:hover,
.list-files-upload::-webkit-scrollbar-thumb:hover {
  background:var(--akcent-color);
}

.progress-bar {
  display: flex;
  align-items:center ;
  width: 100%;
  height: 100%;
  border: 1px solid var(--primary-color);
  text-align: center;
  justify-content: center;
}

.progress {
  align-self: center;
  width: 0%;
  height: 40px;
  background-color: var(--color-succes);
  border-radius: var(--br);
  transition: width 0.3s ease-in-out;
}

.progress-bar  span {
  display: block;
  position: absolute;
  margin-top: 5px;
  color: var(--background-color);
}
.no-filters-message {
  text-align: center;
  padding: 250px;
  color: var(--akcent-color-2);
  font-size: 1.2rem;
}

.icon {
  margin-right: 8px;
  vertical-align: middle;
  color: var(--akcent-color);
} 
.loading-screen{
  height: 100vh;
  color:var(--akcent-color-2);
  text-align: center;
  margin: 0 auto;
  align-items: center;
  padding-top: 150px;
  cursor: wait;
  z-index: 100000;
}
.loading {
  position: absolute;
  top:0;
  width: inherit;
  height: 100vh;
  color:var(--akcent-color-2);
  text-align: center;
  padding-top: 150px;
  cursor: wait;
  z-index: 100000;
}
.loading p{
  animation: fadeInOut 1.5s ease infinite;
  font-size: 64px;
  filter: invert(5);
}
.loading-screen p{
  animation: fadeInOut 1.5s ease infinite;
  font-size: 24px;
}
@keyframes fadeInOut {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Ваш файл стилей */

button {
  transition: transform 0.3s ease; /* добавляем плавный переход для анимации */
}

button:active {
  transform: scale(0.95); /* изменяем размер при нажатии */
}
